.wrapper{
  --textColor: #fff;
  --textHoverColor: #8c8f94;
  --bluredLine: rgba(255, 255, 255, 0.1);
  --bg-main: #1f2123;
  --btn-bg: #323436;
  background-color: var(--bg-main);
  color: var(--textColor);
  font-size: 1rem;
  padding: 0 20%;
  transition: padding .5s;
}
.wrapper.light{
  --textColor: #1f2123;
  --textHoverColor: rgb(1, 56, 159);
  --bluredLine: #8c8f94;
  --bg-main: #fff;
  --btn-bg: #ccc8;
  }

.header{
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: .875em;

  .logo{
    flex: 1;
    font-size: 1.25rem;
    display: flex;
    align-items: center;
    
    &>a{
      display: flex;
      flex-direction: row;
      align-items: center;

      img{
        margin-right: 1em;
        width: 2rem;
        height: 2rem;
        object-fit: contain;
      }
    }
  }

  .toggle{
    padding: 1.25rem 0;
    color: #113ead;
    cursor: pointer;
    display: none;
  }

  .menu{
    display: flex;
    flex-direction: row;
    gap: 1rem;
    padding: 1.25rem 0;
    
    .item{
      cursor: pointer;
      line-height: 2;
    }
    .item:hover:not(:last-child){
      color: var(--textHoverColor);
    }
    .login{
      padding-left: 1rem;
      border-left: 1px solid var(--bluredLine)
    } 
    .signup{
      padding: 0 0.75rem;
      border: 1px solid var(--bluredLine);
      border-radius: .25rem;

      &:hover{
        border-color: var(--textHoverColor);
      }
    }
  }
}

.body{
  margin-top: 2rem;
}

